import React from 'react';
import './Cognitration.css';

// Import all images from ./img
function importAll(r) {
	let images = {};
	r.keys().map((item, index) => { return images[item.replace('./', '')] = r(item); });
	return images;
}

const images = importAll(require.context('./img', false, /\.(png|jpe?g|svg)$/));

/**
 * Cognitration is a tile matching game where
 * you click on a tile to reveal an image and then try to click on the matching tiles in the
 * grid even though it is covered. If you match a set of tiles, those tiles disappear and
 * reveal part of the image below. When all of the tiles are matched a victory animation runs.
 * Users only get a certain number of attempts and a certain number of minutes to match all
 * of the tiles. The user is presented with reset buttons throughout the stages of the game
 * to start over.
 */
class Cognitration extends React.Component {
	/**
	 * Initials class variables and sets the initial state
	 */
	constructor() {
		super();
		this.startPhase = 'start';
		this.playPhase = 'play';
		this.victoryPhase = 'victory';
		this.lossPhase = 'loss';
		this.initialState = {
			phase: this.startPhase, // Current phase of the game: 'start'|'play'|'victory'|'loss'
			score: 0, // Current player score
			secondsLeft: 5 * 60, // Seconds left in the current game
			attemptsLeft: 100, // Number of attempts the user still has to flip all the tiles
			tiles: [], // The tiles that make up the game board
			reward: {}, // The set of images that animate in when the user wins
		}
		this.tileShowingImageClass = 'show-image';
		this.tileConqueredClass = 'conquered';
		this.tileHidingImageClass = '';
		this.tileImageUrls = [
			images['logo01_2x.png'],
			images['logo02_2x.png'],
			images['logo03_2x.png'],
			images['logo04_2x.png'],
			images['logo05_2x.png'],
			images['logo06_2x.png'],
			images['logo07_2x.png'],
			images['logo08_2x.png'],
			images['logo09_2x.png'],
			images['logo10_2x.png'],
			images['logo11_2x.png'],
			images['logo12_2x.png'],
			images['logo13_2x.png']
		];
		this.defaultBackgroundUrl = 'data:image/png;base64,';
		this.extraTileImageUrl = images['extra_2x.png'];
		this.lossScreenImages = {
			bg: this.defaultBackgroundUrl,
			text: images['arched6_oops_2x.png'],
			textSize: {w: 308, h: 61}
		}
		this.rewardImageSets = [
			{
				bottle: images['bottle1_2x.png'],
				text: images['arched1_blue_2x.png'],
				bg: images['background1_2x.png'],
				textSize: {w: 344, h: 73},
				bottleSize: {w: 150, h: 281}
			},
			{
				bottle: images['bottle2_2x.png'],
				text: images['arched2_green_2x.png'],
				bg: images['background2_2x.png'],
				textSize: {w: 344, h: 73},
				bottleSize: {w: 140, h: 282}
			},
			{
				bottle: images['bottle3_2x.png'],
				text: images['arched3_orange_2x.png'],
				bg: images['background3_2x.png'],
				textSize: {w: 344, h: 73},
				bottleSize: {w: 202, h: 281}

			},
			{
				bottle: images['bottle4_2x.png'],
				text: images['arched4_cyan_2x.png'],
				bg: images['background4_2x.png'],
				textSize: {w: 344, h: 73},
				bottleSize: {w: 111, h: 277}
			},
			{
				bottle: images['bottle5_2x.png'],
				text: images['arched5_darkgreen_2x.png'],
				bg: images['background5_2x.png'],
				textSize: {w: 344, h: 73},
				bottleSize: {w: 111, h: 277}
			},
		];
		this.pointsPerTileSet = 10;
		this.rewardDisplayDelay = 500;
		this.mismatchHideDelay = 800;
		this.tileMatchDelay = 1000/4;
		this.state = this.getResetState(this.initialState.phase);
		this.preloadLargeImages();
	}

	/**
	 * Builds the data for an individual tile
	 * @param  {string} imageUrl URL of the image the tile will display
	 * @param  {number} id unique identifier number for this tile
	 * @return object 
	 */
	buildTileFromImageUrl(imageUrl, id) {
		return {id: id, statusClass: '', imageUrl: imageUrl};
	}

	/**
	 * Builds an array of 25 tiles in a random order using pairs of 12 tiles and one extra tile
	 * @return {array} 25 tiles in a random order
	 */
	buildTiles() {
		let imageUrls = this.shuffle(this.tileImageUrls.slice()).slice(0, 12);
		let doubledTiles = [];
		let id = 1;

		imageUrls.forEach((imageUrl) => {
			doubledTiles.push(this.buildTileFromImageUrl(imageUrl, id++), this.buildTileFromImageUrl(imageUrl, id++));
		});

		doubledTiles.push(this.buildTileFromImageUrl(this.extraTileImageUrl, id++));

		return this.shuffle(doubledTiles);
	}

	/**
	 * Shuffles an array
	 * @param  {array} any array
	 * @return {array} same array but in a new order
	 */
	shuffle(array) {
		var currentIndex = array.length, temporaryValue, randomIndex;

		// While there remain elements to shuffle...
		while (0 !== currentIndex) {

			// Pick a remaining element...
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex -= 1;

			// And swap it with the current element.
			temporaryValue = array[currentIndex];
			array[currentIndex] = array[randomIndex];
			array[randomIndex] = temporaryValue;
		}

		return array;
	}

	/**
	 * Resets the game with a new gameboard, zero score, full attempts, and full timer
	 */
	resetAndPlay() {
		this.setState(this.getResetState(this.playPhase));
		this.initTimer();
	}

	/**
	 * Gets the state of the game as a specific phase
	 * @param  {string} phase Phase of the game:'start'|'play'|'loss'|'victory'
	 * @return {object} state object
	 */
	getResetState(phase) {
		return {
			phase: phase,
			score: this.initialState.score,
			secondsLeft: this.initialState.secondsLeft,
			attemptsLeft: this.initialState.attemptsLeft,
			tiles: this.buildTiles(),
			reward: this.getRandomReward()
		};
	}

	/**
	 * Gets a random reward 
	 * @return {object} Set of images for the reward screen
	 */
	getRandomReward() {
		return this.rewardImageSets[Math.floor(Math.random() * this.rewardImageSets.length)]
	}

	/**
	 * Is this tile revealed?
	 * @param  {number}  idx Index of the tile in state.tiles
	 * @return {boolean}
	 */
	isTileShowingImage(idx) {
		return this.state.tiles[idx].statusClass === this.tileShowingImageClass;
	}

	/**
	 * Has this tile already been matched with its duplicate?
	 * @param  {number}  idx Index of the tile in state.tiles
	 * @return {boolean}
	 */
	isTileConquered(idx) {
		return this.state.tiles[idx].statusClass === this.tileConqueredClass;
	}

	/**
	 * Is this tile hiding its image?
	 * @param  {number}  idx Index of the tile in state.tiles
	 * @return {boolean}
	 */
	isTileHidingImage(idx) {
		return this.state.tiles[idx].statusClass === this.tileHidingImageClass;
	}

	/**
	 * Do these two tiles have the same image?
	 * @param  {number} idx1 Index of tile 1 in state.tiles
	 * @param  {number} idx2 Index of tile 2 in state.tiles
	 * @return {boolean}
	 */
	doTilesMatch(idx1, idx2) {
		return this.state.tiles[idx1].imageUrl === this.state.tiles[idx2].imageUrl;
	}

	/**
	 * Show a tile's image
	 * @param  {number]} idx Index of the tile in state.tiles
	 */
	showTileImage(idx) {
		let tiles = this.state.tiles.slice()
		tiles[idx].statusClass = this.tileShowingImageClass
		this.setState({tiles: tiles});
	}

	/**
	 * Hide a tile's image
	 * @param  {[type]} idx Index of tile in state.tiles
	 */
	hideTileImage(idx) {
		let tiles = this.state.tiles.slice()
		tiles[idx].statusClass = this.tileHidingImageClass;
		this.setState({tiles: tiles});
	}

	/**
	 * Match a pair of tiles (thus removing them from play)
	 * @param  {number} idx1 Index of tile1 in state.tiles
	 * @param  {number} idx2 Index of tile2 in state.tiles
	 */
	conquerTiles(idx1, idx2) {
		let tiles = this.state.tiles.slice();
		let conqueredClass = 'conquered';
		tiles[idx1].statusClass = conqueredClass;
		tiles[idx2].statusClass = conqueredClass;
		this.setState({tiles: tiles});
	}

	/**
	 * Handles when a win is triggered
	 */
	handleVictory() {
		this.setState({'phase' : this.victoryPhase});
	}

	/**
	 * Handles when user runs out of play attempts
	 */
	handleOutOfAttempts() {
		this.setState({'phase': this.lossPhase});
	}

	/**
	 * Handles when user runs out of time
	 */
	handleTimeUp() {
		this.setState({'phase': this.lossPhase});
	}

	/**
	 * Adds a certain amount of points to the user's score
	 * @param  {number} points Points to add to score
	 */
	incrementScore(points) {
		this.setState({score: this.state.score + points});
	}

	/**
	 * Starts the game timer
	 */
	initTimer() {
		if(this.hasOwnProperty('timerId')){
			clearInterval(this.timerId);
		} 

		this.timerId = setInterval(() => {
			let secondsLeft = this.state.secondsLeft;
			this.setState({secondsLeft: secondsLeft - 1});

			if(secondsLeft <= 1) {
				clearInterval(this.timerId);
				this.handleTimeUp()
			} 
		}, 1000);
	}

	/**
	 * Handles when two tiles match
	 * @param  {number} idx1 Index of a tile in state.tiles
	 * @param  {number} idx2 Index of a tile in state.tiles
	 */
	handleMatch(idx1, idx2) {
		this.incrementScore(this.pointsPerTileSet);
		setTimeout(() => {
			this.conquerTiles(idx1, idx2);
			if(this.areAllTilesConquered()) {
				this.handleVictory();
			}
		}, this.tileMatchDelay);
	}

	/**
	 * Are all the tiles conquered (is the board clear)?
	 * @return {boolean}
	 */
	areAllTilesConquered() {
		let tiles = this.state.tiles;
		let unConqueredTileIdx = tiles.findIndex(tile => tile.statusClass !== this.tileConqueredClass)
		return unConqueredTileIdx === -1
	}

	/**
	 * Core of the game, handles what happens when a user clicks on a tile
	 * @param  {number} i Index of the tile that was clicked
	 */
	handleTileClick(idx) {
		let tiles = this.state.tiles.slice();

		// Is this tile already showing its image or conquered? Bail.
		if(this.isTileShowingImage(idx) || this.isTileConquered(idx)) return;

		// How many tiles are already showing their images?
		let tilesShowingImages = tiles.filter(
			(aTile, aTileIdx) => {
				return this.isTileShowingImage(aTileIdx);
			} 
		);

		//If two tiles are already showing images, bail
		if(tilesShowingImages.length >= 2) return;

		// Show the tile image you clicked on
		this.showTileImage(idx);

		// How many tiles are conquered?
		let conqueredTiles = tiles.filter(
			(aTile, aTileIdx) => {
				return this.isTileConquered(aTileIdx);
			} 
		);

		// If this is the last unconquered tile, you win
		if(conqueredTiles.length === tiles.length - 1) {
			return setTimeout(this.handleVictory.bind(this), this.rewardDisplayDelay);
		}
		
		// If there is another flipped tile, this is an attempted match
		if(tilesShowingImages.length === 1) {
			let otherTileShowingImage = tilesShowingImages[0];
			
			// Decrement attempts left and end game if user is out of attempts
			this.setState({attemptsLeft: this.state.attemptsLeft - 1});
			if(this.state.attemptsLeft <= 1) {
				this.handleOutOfAttempts();
			}
			
			//If the tiles match, clear them from board
			let otherIdx = tiles.indexOf(otherTileShowingImage);
			if(this.doTilesMatch(idx, otherIdx)) {
				this.handleMatch(idx, otherIdx);
			// If the tile's don't match, hide their images after a short delay
			} else {
				setTimeout(() => {
					this.hideTileImage(idx);
					this.hideTileImage(otherIdx);  
				}, this.mismatchHideDelay);
			}
		}
	}

	/**
	 * Preloads images above 10k because they are large enough to take time to appear
	 * when they are injected into the DOM
	 */
	preloadLargeImages() {
		var images1 = [];
		var images2 = [];
		var largeImages = this.rewardImageSets;
		largeImages.forEach((set, i) => {
			images1[i] = new Image();
			images1[i].src = set.bg;
			images2[i] = new Image();
			images2[i].src = set.bottle;
		});
	}

	/**
	 * Renders the game
	 * @return {JSX} The appropriate screen for the phase of the game
	 */
	render() {
		if(this.state.phase === this.startPhase) {
			return (
				<StartScreen 
					handlePlayClick={this.resetAndPlay.bind(this)} 
					bg={this.defaultBackgroundUrl}
				/>
			);
		} else if(this.state.phase === this.playPhase) {
			return (
				<GameBoard 
					tiles={this.state.tiles} 
					secondsLeft={this.state.secondsLeft}
					attemptsLeft={this.state.attemptsLeft}
					score={this.state.score}
					handleTileClick={this.handleTileClick.bind(this)}
					handleResetClick={this.resetAndPlay.bind(this)}
					reward={this.state.reward}
				/>
			);
		} else if(this.state.phase === this.lossPhase) {
			return (
				<LossScreen 
					images={this.lossScreenImages}
					handleResetClick={this.resetAndPlay.bind(this)} 
				/>
			);
		} else if(this.state.phase === this.victoryPhase) {
			return (
				<VictoryScreen 
					handleResetClick={this.resetAndPlay.bind(this)} 
					reward={this.state.reward}
				/>
			);
		}
	};
}

/**
 * The first screen the user sees, displays a play button to start the game
 */
class StartScreen extends React.Component {

	/**
	 * Renders the start screen
	 */
	render (){
		return (
			<div className="screen start-screen" style={{backgroundImage: 'url('+this.props.bg+')'}}>
				<div className="start-screen-inner">
					<div className="start-screen-instructions">
						<strong>Instructions: </strong> The object of this game is to turn over pairs of matching cards. Once all cards are matched, a secret image will be revealed.
					</div>
					<NewGameButton 
						onClick={this.props.handlePlayClick} 
						typeClass='game-button-play'
						sizeClass="game-button-large"
					/>
				</div>
				<div className="stars">
					<div className="star star-1 star-lg" />
					<div className="star star-2 star-lg" />
					<div className="star star-3 star-lg" />
					<div className="star star-4 star-lg" />
					<div className="star star-5 star-md" />
					<div className="star star-6 star-md" />
					<div className="star star-7 star-sm" />
					<div className="star star-8 star-sm" />
				</div>
			</div>
		);
	}
}

/**
 * The screen the user sees when they run out of time or attempts.
 * Provides a button to try again
 */
class LossScreen extends React.Component {

	/**
	 * Renders the loss screen
	 */
	render (){
		return (
			<div className="screen loss-screen" style={{backgroundImage: 'url('+this.props.images.bg+')'}}>
				<img className="loss-text-image" src={this.props.images.text} width={this.props.images.textSize.w} height={this.props.images.textSize.h}  alt="Oops, you didn't make it. Try again."/>
				<NewGameButton 
					onClick={this.props.handleResetClick} 
					typeClass='game-button-replay'
					sizeClass="game-button-large"
				/>
			</div>
		);
	}
}

/**
 * Screen that the user sees when they win the game. Displays
 * an animation.
 */
class VictoryScreen extends React.Component {
	/**
	 * Renders the victory screen
	 */
	render (){
		return (
			<div className="screen victory-screen" style={{backgroundImage: "url("+this.props.reward.bg+")"}}>
				<img className="bottle-image" src={this.props.reward.bottle} width={this.props.reward.bottleSize.w} height={this.props.reward.bottleSize.h} alt="" />
				<img className="victory-text-image" src={this.props.reward.text} width={this.props.reward.textSize.w} height={this.props.reward.textSize.h} alt ="Congratulations! Call Cognito to clean up and reveal your identity."/>
				<NewGameButton 
					onClick={this.props.handleResetClick} 
					typeClass='game-button-replay'
					sizeClass="game-button-large"
				/>
				<div className="stars">
					<div className="star star-1 star-lg" />
					<div className="star star-2 star-lg" />
					<div className="star star-3 star-md" />
					<div className="star star-4 star-md" />
					<div className="star star-5 star-sm" />
				</div>
			</div>
		);
	}
}

/**
 * A button that starts the game over with a fresh timer, score, tiles, reward, and number of attempts
 */
class NewGameButton extends React.Component {
	render() {
		return (
				<button 
					className={"game-button " + this.props.typeClass + " " + this.props.sizeClass}
					onClick={this.props.onClick}
				>
					Start Game
				</button>
			);
	}
}

/**
 * One unit in the game's grid. Shows an image when clicked
 */
class Tile extends React.Component {
	render() {
		return (
			<li 
				className={"tile " + this.props.statusClass}
				onClick={this.props.handleClick}
				style={{ backgroundImage: 'url(' + this.props.imageUrl + ')'}}
			>
				<div className="tile-img-cover" />
				<div className='stars'>
					<div className="star star-1 star-md" />
					<div className="star star-2 star-sm" />
					<div className="star star-3 star-sm" />
				</div>
			</li>
		);
	}
}

/**
 * Timer that counts down while the user plays. If it reaches zero, the user loses.
 */
class Timer extends React.Component {
	render() {
		return (
			<div className="stat-box timer">
				<div className="stat-box-label">Timer:</div>
				<div className="stat-box-number">{this.formatTime(this.props.secondsLeft)}</div>
			</div>
		);
	}

	/**
	 * Takes time in seconds and changes it to minutes:seconds with leading zeros.
	 * @param  {number} seconds 
	 * @return {string} seconds in minutes:seconds format with leading zeros
	 */
	formatTime(seconds) {
		let timerMinutes = Math.floor(seconds/60);
		let timerSeconds = seconds%60;
		if(timerSeconds < 10) {
			timerSeconds = "0" + timerSeconds;
		}
		return timerMinutes + ':' + timerSeconds;
	}
}

/**
 * Shows the score of the game
 */
class ScoreBox extends React.Component {
	render() {
		return (
			<div className="stat-box score-box">
				<div className="stat-box-label">Score:</div>
				<div className="stat-box-number">{this.props.score}</div>
			</div>
		);
	}
}

/**
 * Shows the number of attempts remaining before the user loses.
 */
class AttemptsBox extends React.Component {
	render() {
		return (
			<div className="stat-box attempts-box"><div className="stat-box-label">Chances:</div><div className="stat-box-number">{this.props.attemptsLeft}</div></div>
		);
	}
}

/**
 * A grid of 25 tiles that the user attempts to match by flipping them each over
 */
class TileGrid extends React.Component {

	/**
	 * Render a single tile
	 * @param  {number} idx Index of the tile
	 * @return {JSX}
	 */
	renderTile(idx) {
		let tile = this.props.tiles[idx];
		return (
			<Tile
				key={tile.id}  
				id={tile.id} 
				statusClass={tile.statusClass} 
				imageUrl={tile.imageUrl} 
				handleClick={() => this.props.handleTileClick(idx)} 
			/>
		)
	}
	
	render() {
		const tiles = this.props.tiles.map((v,i) => this.renderTile(i));
		
		return (
			<ul className="tile-grid" style={{backgroundImage: "url("+this.props.bg+")"}}>
				{tiles}
			</ul>
		);
	}
}

/**
 * Displays the grid of tiles, the score, the attempts left,
 * the timer, and a reset button, i.e. the main playable part of the game.
 */
class GameBoard extends React.Component {

	render() {
		return (
			<div className="game-board screen">
				<TileGrid 
					bg={this.props.reward.bg}
					tiles={this.props.tiles}
					handleTileClick={this.props.handleTileClick}
				/>
				<div className="stat-box-wrap">
					<ScoreBox score={this.props.score} />
					<Timer secondsLeft={this.props.secondsLeft} />
					<AttemptsBox attemptsLeft={this.props.attemptsLeft} />
					<NewGameButton 
						onClick={this.props.handleResetClick} 
						typeClass='game-button-restart'
						sizeClass='game-button-small'
					/>
				</div>
			</div>
		);
	}
}

export default Cognitration;


